.navGame{
    width: 100%;
    border-radius: 10px;
    border: 3px solid var(--Header-Outline);
    max-width: var(--max-width);
    display: flex;
    justify-content: space-between;
    padding: 1em;
    margin: 0 auto;
    transform: translateY(-150%);
    animation: Appear 1s forwards;
}

@keyframes Appear {
    from{
        transform: translateY(-150%);
    }
    to{
        transform: translateY(0%);
    }
}

.navGame .boxes{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.navGame .box-text{
    color: #fff;
    text-transform: uppercase;
    font-size: 2em;
    text-align: left;
}

.navGame .box-text span{
    padding: 0;
    line-height: .8em;
}

.navGame .box-score{
    background-color: #fff;
    padding: 1em 2em;
    border-radius: 10px;
    letter-spacing: .1em;
}

.navGame .box-score .text{
    text-transform: uppercase;
    color: var(--Score-Text);
    text-align: center;
    font-weight: bolder;
}

.navGame .box-score .score{
    font-size: 4em;
    color:var(--Dark-Text);
    line-height: 1em;
    margin: auto;
    font-weight: bolder;
}

.navGame .box-score .score.red{
    color: hsl(349, 70%, 56%);
}

.navGame .box-score .score.green{
    color: hsl(125, 58%, 57%);
}

