.game.inGame {
    padding: 0;
    font-size: 1em;
}

.game.inGame .boxes{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 1em;
    align-items: center;
    animation: none;
    transform: none;
    transform: scale(.3);
    animation: appearInGame 2s forwards;
    pointer-events: none;
}

@keyframes appearInGame {
    0%{
        transform:scale(.3);
        pointer-events: none;
    }
    100%{
        transform:scale(1);
        pointer-events: all;
    }
}

.game.inGame .box{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.game.inGame .box-img{
    position: static;
    transform: none;
    cursor: auto;
}

.game.inGame .box-img:hover{
    box-shadow: none;
}

.game.inGame .box-img.win,
.game.inGame .box-img.win{
    box-shadow: rgb(255 255 255 / 5%) 0px 0px 0px 1.5em, rgb(255 255 255 / 3%) 0px 0px 0px 3em, rgb(255 255 255 / 1%) 0px 0px 0px 4.5em;
}

.game.inGame .selecting{
    width: 8em;
    height: 8em;
    background-color: #00000050;
    border-radius: 50%;
    padding-top: .5em;
    margin: 0 auto;
    animation: selecting 2s infinite;
}

@keyframes selecting {
    0%{
        background-color: #00000030;
        transform: scale(.5);
    }
    40%{
        background-color: #00000060;
        transform: scale(1);
        box-shadow: #00000060 0px 0px 0px 0em;
    }
    70%{
        background-color: #00000060;
        transform: scale(.9);
        box-shadow: #00000000 0px 0px 0px 1.5em;
    }
    100%{
        background-color: #00000030;
        transform: scale(.5);
    }
}

.game.inGame .boxes .box .text{
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    padding: 1em 0;
}

@media screen and (min-width:866px){
    .game.inGame {
        font-size: 1.4em;
    }
    .game.inGame .boxes{
        flex-wrap: nowrap;
    }
    .game.inGame .box:nth-child(2){
        order: 3;
    }
    .game.inGame .result-container{
        order: 2;
        width: 100%;
    }
    .game.inGame .boxes .box .text{
        font-size: .81em;
    }
}

