.button-rules{
    background-color: transparent;
    border: 2px solid var(--Header-Outline);
    color: var(--Header-Outline);
    padding: .5em 2em;
    border-radius: 10px;
    bottom: 100px;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    font-weight: bolder;
    font-size: 16px;
    transition: color .5s ease-in, border-color .5s ease-in, background-color .5s ease-in;
}

.button-rules:hover{
    border: 2px solid #fff;
    background-color: #fff;
    color: var(--Dark-Text);
}

.bg-rules{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 1s, background-color 1s;
    z-index: 3000;
    background-color: transparent;
}

.rules{
    background-color: #fff;
    border-radius: 10px;
}

.rules .title{
    color: var(--Dark-Text);
    font-size: 2em;
}

.rules .container-button{
    padding: 1em;
}

.rules .container-button img{
    width: 2em;
    cursor: pointer;
    transition: transform .3s ease-in;
}

.rules .container-button img:hover{
    transform: scale(.8) rotate(180deg);
}

@media screen and (max-width:865px) {
    .bg-rules{
        transform: translateY(100%);
    }   
    .bg-rules.open{
        transform: translateY(0%);
        animation: movil-bg 1s forwards;
        animation-delay: 1s;
    }
    .rules{
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 0%;
    }
    .open .rules{
        animation: movil-rules 1s forwards;
        animation-delay: 1s;
    }
}

@media screen and (min-width:866px) {
    .button-rules{
        margin: 0 2em 0 auto;
    }
    .rules{
        background-color: #fff;
        padding: 2em 3em;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
    }
    .bg-rules{
        background-color: #00000000;
        transform: translateY(100%);
        box-shadow: 0 0 10em rgba(0, 0, 0, 0.007) inset;
    }   
    .bg-rules.open{
        transform: translateY(0%);
        animation: desktop 1s forwards;
        animation-delay: 1s;
    }
    .rules .title{
        margin-bottom: 50px;
    }
    .rules .container-button{
        position: absolute;
        top: 1.5em;
        right: 2em;
    }
    .rules .container-button img{
        width: 2em;
        cursor: pointer;
    }
}

@keyframes movil-rules{
    from{
        min-height: 0%;
    }
    to{
        min-height: 80%;
    }
}

@keyframes movil-bg {
    from{
        background-color: #ffffff00;
    }
    to{
        background-color: #ffffff;
    }
}
@keyframes desktop {
    from{
        background-color: #00000000;
        box-shadow: 0 0 10em rgba(0, 0, 0, 0) inset;
    }
    to{
        background-color: #00000099;
        box-shadow: 0 0 10em rgba(0, 0, 0) inset;
    }
}