.App {
  min-height: 100vh;
  background: var(--bg-movil);
  padding: 3em 0 1em;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 1fr;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.h1{
  display: none;
}

@media screen and (min-width:866px){
  .App {
    background: var(--bg-desktop);
  }
}