.nav-container{
    position: absolute;
}

.nav{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 3em 1em;
    transition: width .5s;
}
.nav.no-home{
    background-color: #00000099;
    box-shadow: 0 0 10em #000 inset;
    transform: translate(-100%);
    transition: transform 1s;
    z-index: 1000;
}
.open .nav.no-home{
    transform: translate(0%);
}

.nav .ul{
    position: relative;
    display: flex;
    gap: 1em;
    text-align: left;
    z-index: 1500;
    flex-direction: column;
}

.nav .li{
    position: relative; 
    list-style: none;
}

.nav .text{
    position: relative;
    font-size: clamp(1em,5vw,2em);
    text-decoration: none;
    letter-spacing: .1em;
    text-transform: uppercase;
    /* color: transparent;
    -webkit-text-stroke: 1px #000; */
    color: #00000050;
    transition: font-size .5s;
    font-size: clamp(2em,6vw,10em);
}
.nav.no-home .text{
    color: #ffffff55;
}

.nav .text:before{
    content: attr(data-text);
    position: absolute;
    color: #fff;
    width: 0%;
    border-right: .1em solid #fff;
    border-left: .1em solid #fff;
    overflow: hidden;
    border-radius: 4px;
    transition: .5s;
}

.nav .text:hover:before{
    width: 100%;
    padding: 0 .3em;
    /* -webkit-text-stroke: 2px #fff; */
    text-shadow: 0 0 .1em var(--color-react),0 0 .1em var(--color-react);
}

.nav .text.select:before{
    width: 100%;
    padding: 0 .3em;
    /* -webkit-text-stroke: 2px #fff; */
    text-shadow: 0 0 .1em var(--color-react),0 0 .1em var(--color-react);
}

.nav.no-home .button{
    font-size: clamp(1em,5vw,2em);
    position: absolute;
    z-index: 2000;
    bottom: 100%;
    right: 0%;
    transform: translateX(100%) translateY(100%);
    background-color: transparent;
    cursor: pointer;
}

.open .nav.no-home .button{
    top: 10px;
    right: 0;
    transform: translateX(-100%);
    font-size: clamp(2em,5vw,6em);
}

.nav.no-home .button button{
    background-color: transparent;
    border: 0;
    cursor: pointer;
}

@media screen and (min-width:866px){
    .nav{
        justify-content: flex-start;
        align-items: center;
    }
}