.game{
    width: 100%;
    padding: 2em;
    max-width: var(--max-width);
    display: flex;
    justify-content: center;
    margin: 3.5em auto 0em;
}

.game.bonusGame{
    margin: 4em auto;
}

.game .boxes{
    position: relative;
    display: inline-block;
    transform: translate(200%);
    animation: appearGame 2s forwards;
    pointer-events: none;
}

@keyframes appearGame {
    0%{
        transform: translate(200%) scale(.3) rotate(360deg);
        pointer-events: none;
    }
    50%{
        transform: translate(0%) scale(.3) rotate(180deg);
        pointer-events: none;
    }
    99%{
        transform: translate(0%) scale(1) rotate(0deg);
        pointer-events: none;
    }
    100%{
        transform: translate(0%) scale(1) rotate(0deg);
        pointer-events: all;
    }
}

.game .boxes .bg{
    margin: auto;
    display: block;
    width: 180px;
    margin-bottom: 50px;
}

.game .box-img, .game .container-img{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    border-radius: 50%;
}

.game .box-img{
    width: 8em;
    height: 8em;
    border-bottom: #00000060 .3em solid;
    /* padding-top: .3em; */
    cursor: pointer;
    transition: box-shadow 1s;
    box-shadow: 0 3px 0px #00000030;
}

.game .box-img:hover{
    box-shadow: rgb(255 255 255 / 5%) 0px 0px 0px 1.5em, rgb(255 255 255 / 3%) 0px 0px 0px 3em, rgb(255 255 255 / 1%) 0px 0px 0px 4.5em;
}

.game .box-img .container-img{
    background-color: #fff;
    width: 6em;
    height: 6em;
    border-top: .3em #00000030 solid;
    /* padding-bottom: .3em; */
}

.game .box-img .container-img img{
    pointer-events: none;
    width: 3em;
}

.game .box-img-1{
    top: 0;
    transform: translateX(-50%) translateY(-50%);
    background: var(--Paper-Gradient);
}

.game .box-img-2{
    top: 0;
    right: 0;
    transform: translateX(50%) translateY(-50%);
    background: var(--Scissors-Gradient);
}

.game .box-img-3{
    bottom: 0;
    right: 50%;
    transform: translateX(50%) translateY(0%);
    background: var(--Rock-Gradient);
}

/* ------------------ Bonus Game ----------------------- */

.game.bonusGame .boxes .bg{
    width: 220px;
    margin-bottom: 0;
}

.bonusGame.game .box-img-1{
    top: 35%;
    right: 0%;
    transform: translateX(60%) translateY(-50%);
}

.bonusGame.game .box-img-2{
    top: 0;
    right: 50%;
    transform: translateX(50%) translateY(-60%);
}

.bonusGame.game .box-img-3{
    bottom: 0%;
    right: 20%;
    transform: translateX(60%) translateY(60%);
}

.bonusGame.game .box-img-4{ 
    bottom: 65%;
    right: 100%;
    transform: translateX(40%) translateY(50%);
    background: var(--Cyan);
}

.bonusGame.game .box-img-5{
    bottom: 0%;
    right: 80%;
    transform: translateX(40%) translateY(60%);
    background: var(--Lizard-Gradient);
}

@media screen and (min-width:866px){   
    .game{
        margin: 100px auto;
    }
    .game .boxes .bg{
        width: 400px;
    }
    .game .boxes{
        font-size: 1.5em;
    }
    .game.bonusGame .boxes .bg{
        width: 400px;
    }
}