.game.inGame .result-container{
    width: 100%;
    height: 8em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.game.inGame .result-message{
    text-align: center;
    transform: scale(0);
    transition: transform 1s;
}

.game.inGame .endGame .result-message{
    transform: scale(1);
}

.game.inGame .result-text{  
    text-transform: uppercase;
    color: #fff;
    font-size: 50px;
    padding: .2em .1em;
}

.game.inGame .result-buton{
    font-size: 18px;
    border: 2px solid #fff;
    text-transform: uppercase;
    color: var(--Dark-Text);
    background-color: #fff;
    width: 100%;
    padding: .8em;
    border-radius: 10px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 .5em .5em #00000030;
    transition: border-color .5s ease, background-color .5s ease;
}

.game.inGame .result-buton:hover{
    background-color: transparent;
    border: 2px solid var(--Header-Outline);
    color: #fff;
}

@media screen and (min-width:866px){
    .game.inGame .boxes .result-container{
        width: 0%;
        overflow: hidden;
        transition: width 1s;
    }
    .game.inGame .boxes .result-container.endGame{
        width: 100%;
        overflow: hidden;
    }
    .game.inGame .endGame .result-message{
        transform: scale(1);
    }
    
    .game.inGame .endGame .result-text{
        min-width: 4em;
    }
}
