:root {
  /* The designs were created to the following widths: */
  /*Desktop*/
  /* --max-width: 1366px;  */
  --max-width: 966px; 
  /*Mobile*/
  --min-width: 375px; 

  /* ## Colors */

  /* ### Primary */
  --Scissors-Gradient: linear-gradient(to top,hsl(39, 89%, 49%), hsl(40, 84%, 53%));
  --Paper-Gradient: linear-gradient(to top,hsl(230, 89%, 62%),hsl(230, 89%, 65%));
  --Rock-Gradient: linear-gradient(to top,hsl(349, 71%, 52%), hsl(349, 70%, 56%));
  --Lizard-Gradient: linear-gradient(to top,hsl(261, 73%, 60%), hsl(261, 72%, 63%));
  --Cyan: linear-gradient(to top, hsl(189, 59%, 53%),hsl(189, 58%, 57%));
  /* ### Neutral */
  --Dark-Text: hsl(229, 25%, 31%);
  --Score-Text: hsl(229, 64%, 46%);
  --Header-Outline: hsl(217, 16%, 45%);
  /* ### Background */
  --bg-movil: linear-gradient(hsl(214, 47%, 23%) 10%,hsl(237, 49%, 15%) 100%);
  --bg-desktop: radial-gradient(hsl(214, 47%, 23%) 30%,hsl(237, 49%, 15%) 100%);

  --color-react:#61dafb;
}

body {
  /* ## Fonts */
  /* [Barlow Semi Condensed](https://fonts.google.com/specimen/Barlow+Semi+Condensed) */
  /* Weights: 600, 700  */
  font-family: "Barlow Semi Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}